	module.exports = {"data":{"site":{"siteMetadata":{"title":"Glen Cheney","url":"https://glencheney.com"}},"markdownRemark":{"html":"<p>Odopod’s brand site runs on ExpressJS.</p>\n<h2>Old name, same company</h2>\n<p>When Nurun acquired Odopod, it became Nurun SF. In 2015, after some changes, Odopod was able to use the name again. The old Odopod site had grown outdated and we needed a new one.</p>\n<h2>Backend</h2>\n<p>Odopod.com uses <a href=\"https://www.contentful.com\">Contentful</a> as the CMS. It is quite flexible and provides APIs for querying published (and draft) content and hosting for images and videos. The <a href=\"https://www.npmjs.com/package/contentful\"><code class=\"language-text\">contentful</code></a> package fetches data when the Express server starts. Templates are written in <a href=\"https://www.npmjs.com/package/pug\">Pug</a> (formerly Jade). The site integrated with the Instagram API to search for @odopod photos with certain hashtags as well as the Jobvite API. We used <a href=\"https://www.npmjs.com/package/cheerio\">Cheerio</a> to parse the Jobvite response and modify it before displaying it.</p>\n<h2>Frontend</h2>\n<p>webpack and Gulp are the frontend build system to bundle JS and CSS. I used webpack’s code-splitting to create a commons bundle and page-specific bundles.</p>\n<p>The common CSS and page-level CSS are loaded asynchronously to avoid blocking page rendering on load.</p>\n<h2>Service workers</h2>\n<p>Google’s <a href=\"https://github.com/googlechrome/workbox\">Workbox</a> is fantastic. It makes implementing and managing a service worker simple.</p>\n<h3>Precaching</h3>\n<p>The Workbox service worker allows us to precache all the webpack assets generated during the build process very easily.</p>\n<div class=\"gatsby-highlight\" data-language=\"js\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token comment\">// Precache and provide routes for webpack assets.</span>\nworkbox<span class=\"token punctuation\">.</span>precaching<span class=\"token punctuation\">.</span><span class=\"token function\">precacheAndRoute</span><span class=\"token punctuation\">(</span>self<span class=\"token punctuation\">.</span>__precacheManifest <span class=\"token operator\">||</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<h3>Runtime caching</h3>\n<p>Workbox also lets you set up runtime caching which only saves responses once the browser has requested it. This was useful for caching fonts because odopod.com serves a web font with the <code class=\"language-text\">.woff2</code> file type and a fallback <code class=\"language-text\">.woff</code> file. Only one of these font files should be cached; saving both is a waste of data. To accomplish this, we register a route looking for font files, then tell Workbox to use the cached version of the file when the browser wants it. If Workbox doesn’t have the file yet, it will fetch the font like normal and then store it.</p>\n<div class=\"gatsby-highlight\" data-language=\"js\">\n      <pre class=\"language-js\"><code class=\"language-js\">workbox<span class=\"token punctuation\">.</span>routing<span class=\"token punctuation\">.</span><span class=\"token function\">registerRoute</span><span class=\"token punctuation\">(</span>\n  <span class=\"token regex\">/\\.(?:woff|woff2)$/</span><span class=\"token punctuation\">,</span>\n  workbox<span class=\"token punctuation\">.</span>strategies<span class=\"token punctuation\">.</span><span class=\"token function\">cacheFirst</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n    cacheName<span class=\"token punctuation\">:</span> <span class=\"token string\">'fonts'</span><span class=\"token punctuation\">,</span>\n    plugins<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n      <span class=\"token keyword\">new</span> <span class=\"token class-name\">workbox<span class=\"token punctuation\">.</span>expiration<span class=\"token punctuation\">.</span>Plugin</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n        maxAgeSeconds<span class=\"token punctuation\">:</span> <span class=\"token number\">30</span> <span class=\"token operator\">*</span> <span class=\"token number\">24</span> <span class=\"token operator\">*</span> <span class=\"token number\">60</span> <span class=\"token operator\">*</span> <span class=\"token number\">60</span><span class=\"token punctuation\">,</span>\n      <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span>\n    <span class=\"token punctuation\">]</span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>The odopod.com service worker also does runtime caching for images from odopod.com as well as images from Contentful with a maximum number of entries in the cache for each.</p>\n<h3>Offline page</h3>\n<p>Since we already have a service worker, why not provide a simple “You’re offline” page to the user when there is no Internet connection? I created a new Pug template, added that page (<code class=\"language-text\">/offline</code>) to the assets that Workbox caches, and then watch for navigation events which fail.</p>\n<div class=\"gatsby-highlight\" data-language=\"js\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token comment\">// Cache the offline page.</span>\nworkbox<span class=\"token punctuation\">.</span>precaching<span class=\"token punctuation\">.</span><span class=\"token function\">precache</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">[</span>\n  <span class=\"token punctuation\">{</span> url<span class=\"token punctuation\">:</span> <span class=\"token string\">'/offline'</span><span class=\"token punctuation\">,</span> revision<span class=\"token punctuation\">:</span> <span class=\"token string\">'1'</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">]</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token comment\">// Respond to navigation requests with the offline page if the fetch fails.</span>\nworkbox<span class=\"token punctuation\">.</span>routing<span class=\"token punctuation\">.</span><span class=\"token function\">registerRoute</span><span class=\"token punctuation\">(</span>\n  <span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> event <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> event<span class=\"token punctuation\">.</span>request<span class=\"token punctuation\">.</span>mode <span class=\"token operator\">===</span> <span class=\"token string\">'navigate'</span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> url <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> <span class=\"token function\">fetch</span><span class=\"token punctuation\">(</span>url<span class=\"token punctuation\">.</span>href<span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token keyword\">catch</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=></span> caches<span class=\"token punctuation\">.</span><span class=\"token function\">match</span><span class=\"token punctuation\">(</span><span class=\"token string\">'/offline'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>","frontmatter":{"title":"odopod.com","imageDescription":"Screenshot of the Odopod home page. 'Hi, we’re Odopod — a digital design agency.'","shortDescription":"Odopod’s brand site runs on ExpressJS.","href":"https://www.odopod.com/","tags":["build systems","maintenance","Node"]}},"hero":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.777434312210201,"src":"/static/odopod-com-hero-65cb833225829d05654a572ae985ec12-0fe8f.png","srcSet":"/static/odopod-com-hero-65cb833225829d05654a572ae985ec12-d7863.png 350w,\n/static/odopod-com-hero-65cb833225829d05654a572ae985ec12-c146b.png 700w,\n/static/odopod-com-hero-65cb833225829d05654a572ae985ec12-0fe8f.png 1400w,\n/static/odopod-com-hero-65cb833225829d05654a572ae985ec12-25199.png 2100w,\n/static/odopod-com-hero-65cb833225829d05654a572ae985ec12-9880c.png 2300w","srcWebp":"/static/odopod-com-hero-65cb833225829d05654a572ae985ec12-43730.webp","srcSetWebp":"/static/odopod-com-hero-65cb833225829d05654a572ae985ec12-1130a.webp 350w,\n/static/odopod-com-hero-65cb833225829d05654a572ae985ec12-7944d.webp 700w,\n/static/odopod-com-hero-65cb833225829d05654a572ae985ec12-43730.webp 1400w,\n/static/odopod-com-hero-65cb833225829d05654a572ae985ec12-beeb1.webp 2100w,\n/static/odopod-com-hero-65cb833225829d05654a572ae985ec12-71e2f.webp 2300w","sizes":"(max-width: 1400px) 100vw, 1400px"}}},"pathContext":{"slug":"/odopod-com/","heroImage":"/images/odopod-com-hero.png/","nextProject":{"frontmatter":{"title":"Shuffle"},"fields":{"slug":"/shuffle/"}}}}

